.header{
	background-color: #f9ece3;
	padding-bottom: 23px;
	&__wrapper{
		max-width: 1280px;
		margin: 0 auto;
		padding: 0 20px;
	}
	&__logo{
		float: left;
		width: 21%;
		padding-left: 30px;
		margin-top: 40px;
		.logo-img{
			float: left;
		}
		.logo-text{
			float: left;
			color: #FF1C3D;
			font-size: 36px;
			font-weight: 700;
			text-transform: uppercase;
			margin: 26px 0 0 7px;
			&__add{
				color: #45170D;
				font-size: 11px;
				font-weight: 400;
				text-transform: uppercase;
				display: block;
				text-align: right;
				position: relative;
				&:before{
					background: #dfd3cd;
					position: absolute;
					left: 0;
					top: 6px;
					content: '';
					width: 58px;
					height: 1px;
				}
			}
		}
	}
	&__content{
		float: right;
		width: 72.2%;
		padding-bottom: 20px;
		margin-top: 27px;
	}
	&__top{
		border-bottom: 1px solid #dfd4cd;
		padding-bottom: 18px;
		.tel{
			float: left;
			position: relative;
			width: 19%;
			&__wrap{
				color: #45170D;
				font-size: 18px;
				font-weight: 400;
				text-transform: uppercase;
				background: url(../img/icon/phone-head.png) 0 0 no-repeat;
				background-size: 20px 18px;
				padding-left: 25px;
			}
		}
		.adres{
			float: right;
			color: #45170D;
			font-size: 12px;
			font-weight: 400;
			padding-right: 30px;
			margin-top: 2px;
			width: 36%;
			background: url(../img/icon/marker.png) left 0 no-repeat;
			background-size: 13px 12px;
			position: relative;
			padding-left: 20px;
		}
		.social{
			float: right;
			padding-right: 30px;
			margin-top: -7px;
			&__icon{
				border: 1px solid #DFD3CD;
				-moz-border-radius: 3px;
				-webkit-border-radius: 3px;
				border-radius: 3px;
				width: 30px;
				height: 30px;
				line-height: 23px;
				display: inline-block;
				text-align: center;
				transition: all 300ms ease 0s;
				margin-right: 5px;
				&:last-child{
					margin-right: 0;
				}
				&:hover{
					border: 1px solid rgba(255, 45, 73, 0.36);
				}
			}
		}
		.social__icon--vk{
			background: url(../img/icon/vk.png) 0 0 no-repeat;
			transition: all 300ms ease 0s;
			background-position: center;
			display: inline-block;
			vertical-align: top;
			background-size: 18px 10px;
			width: 30px;
			height: 30px;
			&:hover {
				background: url(../img/icon/vk-hover.png) center center no-repeat;
			}
		}
		.social__icon--fb{
			background: url(../img/icon/fb.png) 0 0 no-repeat;
			transition: all 300ms ease 0s;
			width: 30px;
			height: 30px;
			background-size: 7px 11px;
			vertical-align: top;
			display: inline-block;
			background-position: center;
			&:hover{
				background: url(../img/icon/fb-hover.png) center center no-repeat;
			}
		}
		.social__icon--insta{
			background: url(../img/icon/insta.png) 0 0 no-repeat;
			transition: all 300ms ease 0s;
			background-size: 14px 14px;
			width: 30px;
			height: 30px;
			vertical-align: top;
			display: inline-block;
			background-position: center;
			&:hover{
				background: url(../img/icon/insta-hover.png) center center no-repeat;
			}
		}
		.cart{
			float: right;
			margin-top: -10px;
			&__text{
				font-size: 12px;
				font-weight: 400;
				color: #1F191A;
				display: inline-block;
				vertical-align: middle;
			}
			&__link{
				font-size: 12px;
				display: inline-block;
				vertical-align: middle;
				font-weight: 400;
				color: #FF1C3D;
				border-bottom: 1px dotted #FF1C3D;
				&:hover{
					border-bottom: 1px dotted transparent;
				}
			}
			&__icon{
				background: url(../img/icon/cart.png) 0 0 no-repeat;
				width: 26px;
				height: 35px;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				line-height: 35px;
				margin-right: 2px;
			}
			&__value{
				color: #FF6183;
				font-size: 16px;
				font-weight: 400;
				display: inline-block;
				vertical-align: middle;
			}
		}
	}
	&__navigation{
		padding-top: 50px;
	}
	.nav{
		&__item{
			display: inline-block;
			padding-right: 79px;
			padding-left: 0;
			&:last-child{
				padding-right: 0;
			}
			&:before{
				display: none;
			}
		}
		&__link{
			font-size: 16px;
			font-weight: 300;
			color: #45170D;
			position: relative;
			&:hover{
				color: #FF1C3D;
			}
			&:before{
				border-bottom: 1px solid #ff1c3d;
				width: 50%;
				position: absolute;
				left: 30%;
				bottom: -20px;
				height: 1px;
				content: '';
				opacity: 0;
				transition: all 300ms ease 0s;
			}
			&:hover:before{
				opacity: 1;
				left: 0;
			}
			&:after{
				border-bottom: 1px solid #ff1c3d;
				width: 50%;
				position: absolute;
				right: 30%;
				bottom: -20px;
				height: 1px;
				content: '';
				opacity: 0;
				transition: all 300ms ease 0s;
			}
			&:hover:after{
				opacity: 1;
				right: 0;
			}
		}
		&__mobile{
			width: 36px;
			cursor: pointer;
			display: none;
			-webkit-transition: all 0.5s ease 0s;
			transition: all 0.5s ease 0s;
			span{
				background-color: #ff1c3d;
				border-radius: 2px;
				display: block;
				height: 4px;
				margin-bottom: 4px;
				position: relative;
				width: 100%;
				transition: all 300ms ease 0s;
			}
			&.drop{
				span{
					background-color: #45170d;
				}
			}
		}
		&__list{
			display: block;
			width: 100%;
		}
	}
}
.top_slider{
	position: relative;
	&__in{
		position: relative;
	}
	&__text{
		color: #FBFBFB;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 54px;
		position: absolute;
		top: 50%;
		left: 19%;
		margin-top: -103px;
		letter-spacing: 1px;
	}
	&__text-add{
		color: #FBFBFB;
		display: block;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 104px;
	}
	&__img{
		img{
			width: 100%;
			min-height: 445px;
		}
	}
	&__btn{
		position: absolute;
		top: 61%;
		left: 19%;
		transition: all 300ms ease 0s;
		&:before{
			position: absolute;
			right: -37px;
			top: 0;
			content: '';
			background: url(../img/icon/animate-1.png) 0 0 no-repeat;
			width: 22px;
			height: 35px;
			animation-name: run;
  			animation-duration: 2.5s;
  			animation-iteration-count: infinite;
  			visibility: hidden;
			transition: all 300ms ease 0s;
		}
		&:after{
			position: absolute;
			right: -27px;
			top: 0;
			content: '';
			background: url(../img/icon/animate-1.png) 0 0 no-repeat;
			width: 22px;
			height: 35px;
			animation-name: move;
  			animation-duration: 2.5s;
  			animation-iteration-count: infinite;
  			visibility: hidden;
			transition: all 300ms ease 0s;
		}
		&:hover:before,&:hover:after{
			visibility: visible;
		}
	}
	&__link{
		color: #FBFBFB;
		font-size: 18px;
		font-weight: 300;
		text-transform: uppercase;
		padding: 7px 30px 9px 45px;
		background-color: #ff1c3d;
		display: block;
		&:before{
			width: 0;
			height: 0;
			border-top: 17.5px solid transparent;
			border-left: 18px solid #ff1c3d;
			border-bottom: 17.5px solid transparent;
			position: absolute;
			right: -18px;
			top: 0;
			content: '';
		}
	}
	.slick-dots{
		position: absolute;
		top: 50%;
		right: 15.3%;
		text-align: center;
		margin-top: -36px;
		width: auto;
		button{
			font-size: 0;
			padding: 0;
			height: 14px;
			width: 14px;
			background: rgba(249,236,227,.6);
			border-radius: 50%;
			border: none;
			transition: all 300ms ease 0s;
			outline: none;
			&::-moz-focus-inner {
  				border: 0;
			}
		}
		.slick-active{
			button{
				width: 22px;
				height: 22px;
				background:#b4a95b;
			}
		}
		li{
			margin-bottom: 5px;
			padding-left: 0;
			&:before{
				display: none;
			}
		}
	}
	.animate-one{
		background: url(../img/icon/animate-1.png) 0 0 no-repeat;
		width: 22px;
		height: 35px;
	}
}
.four-block{
	margin-top: 40px;
	&__item{
		float: left;
		width: 25%;
		position: relative;
		padding-left: 0;
		margin-bottom: 0;
		&:before{
			display: none;
		}
	}
	&__img{
		position: relative;
		overflow: hidden;
		&:before{
			background: rgba(20,5,2,.9);
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			content: '';
			transition: all 300ms ease 0s;
			z-index: 1;
		}
		&.add{
			&:before{
				background: rgba(255,28,61,.7);
			}
		}
		&:hover:before{
			background: rgba(20,5,2,.6);
		}
		img{
			display: block;
			width: 100%;
			transition: all 1s ease 0s;
		}
		&:hover img{
			transform: scale(1.2,1.2);
		}
	}
	&__icon{
		position: absolute;
		top: 40px;
		left: 0;
		right: 0;
		text-align: center;
		z-index: 1;
		pointer-events: none;
		i{
			color: #FFF;
			font-size: 52px;
		}
	}
	&__text{
		color: #FBFBFB;
		font-size: 14px;
		font-weight: 300;
		position: absolute;
		left: 0;
		right: 0;
		text-align: center;
		bottom: 35px;
		line-height: 17px;
		z-index: 1;
		pointer-events: none;
	}
	&__nav{
		display: block;
		width: 100%;
	}
}
.catalog{
	margin-top: 61px;
	&__title{
		color: #140502;
		font-size: 60px;
		font-weight: 300;
		text-transform: uppercase;
		float: left;
		background:#FFF;
		position: relative;
	}
	&__view{
		position: relative;
		&:before{
			background:#dfd3cd;
			width: 100%;
			height: 1px;
			position: absolute;
			bottom: 8px;
			left: 0;
			content: '';
		}
	}
	&__img-mobile{
		display: none;
	}
	&__link{
		color: #140502;
		font-size: 18px;
		font-weight: 300;
		text-transform: uppercase;
		float: right;
		margin-top: 37px;
		position: relative;
		background:#FFF;
		padding-left: 20px;
		.icon{
			background: url(../img/icon/arrow-right.png) 0 0 no-repeat;
			width: 8px;
			height: 13px;
			display: inline-block;
			padding-left: 8px;
			vertical-align: middle;
			transition: all 200ms ease 0s;
		}
		&:hover{
			color: #ff1c3d;
		}
		&:hover .icon{
			background: url(../img/icon/arrow-right-red.png) 0 0 no-repeat;
		}
	}
	&__main{
		margin-top: 30px;
	}
	&__ref{
		display: block;
		position: relative;
		&:before{
			background: rgba(20,5,2,.5);
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			content: '';
			opacity: 1;
			transition: all 300ms ease 0s;
		}
		&:hover:before{
			opacity: 0;
		}
	}
	&__text{
		color: #FBFBFB;
		background: rgba(255,28,61,.8);
		padding: 10px 27px;
		font-size: 18px;
		font-weight: 300;
		text-transform: uppercase;
		display: inline-block;
		position: absolute;
		left: 0;
		top: 40px;
	}
	&__nav{
		display: block;
		width: 100%;
	}
	&__item{
		float: left;
		position: relative;
		width: 25%;
		padding-left: 0;
		margin-bottom: 0;
		font-size: inherit;
	    font-weight: normal;
	    line-height: inherit;
		&:before{
			display: none;
		}
		img{
			display: block;
			width: 100%;
		}
		&:first-child{
			width: 50%;
		}
		&:last-child{
			display: none;
		}
	}
	&__add{
		width: 33.3333%;
		float: left;
		padding-left: 0;
		margin-bottom: 0;
		font-size: inherit;
	    font-weight: normal;
	    line-height: inherit;
		&:before{
			display: none;
		}
		img{
			display: block;
			width: 100%;
		}
	}
}
.about{
	margin-top: 70px;
	p{
		color: #140502;
		font-size: 12px;
		font-weight: 400;
		line-height: 14.5px;
		margin-bottom: 13px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&__item{
		margin-bottom: 50px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&__img{
		float: right;
		position: relative;
		margin: 0 0 10px 50px;
		z-index: 1;
		width: 50%;
		img{
			display: block;
			width: 100%;
		}
	}
	.main_title{
		margin-bottom: 32px;
		margin-top: 51px;
		&:first-child{
			margin-top: 0;
		}
	}
	.catalog__link{
		margin-top: 0;
		.icon{
			margin-left: 10px;
		}
	}
	.catalog__view{
		margin-top: 20px;
		overflow: hidden;
		&:before{
			bottom: 1px;
		}
		&.last{
			margin-top: 35px;
		}
	}
}
.new{
	background-color: #f9ece3;
	margin-top: 69px;
	position: relative;
	padding-bottom: 67px;
	&__in{
		padding-top: 70px;
		.main_title{
			float: left;
			background: #f9ece3;
			position: relative;
			padding-right: 15px;
		}
		.paginginfo{
			float: right;
			padding-right: 20px;
			margin-top: 22px;
			background: #f9ece3;
			position: relative;
			padding-left: 40px;
		}
	}
	&__carousel{
		margin-top: 34px;
	}
	&__item{
		position: relative;
		&:hover .new__btn{
			opacity: 1;
		}
		&:hover .new__hidden{
			opacity: 1;
		}
		&:before{
			background: rgba(20,5,2,.4);
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			content: '';
			transition: all 300ms ease 0s;
			border: 10px solid transparent;
		}
		&:hover:before{
			background: none;
			border: 10px solid rgba(20,5,2,.4);
		}
		.share{
			background: rgba(20,5,2,.3);
			width: 50px;
			height: 50px;
			float: left;
			position: relative;
			transition: all 300ms ease 0s;
			&__icon{
				line-height: 50px;
				i{
					color: #FFF;
					font-size: 22px;
					vertical-align: middle;
				}
			}
			&:hover{
				background: rgba(20,5,2,.1);
			}
			&__social{
				position: absolute;
				bottom: 50px;
				left: -50px;
				width: 200px;
				background:#FFF;
				padding: 10px;
				display: none;
				.socail_in{
					display: block;
					text-align: left;
					font-size: 14px;
					margin-bottom: 15px;
					color: #140502;
					&:last-child{
						margin-bottom: 0;
					}
					&:hover{
						color: #ff1c3d;
					}
				}
			}
		}
		.view{
			background: rgba(20,5,2,.3);
			width: 50px;
			height: 50px;
			float: left;
			transition: all 300ms ease 0s;
			&__icon{
				line-height: 50px;
				img{
					display: inline-block;
					width: 23px;
					margin: auto;
					vertical-align: middle;
				}
			}
			&:hover{
				background: rgba(20,5,2,.2);
			}
		}
	}
	&__wrapper{
		padding: 0 10px;
	}
	&__img{
		img{
			display: block;
			width: 100%;
			height: 310px;
			object-fit: cover;
		}
	}
	&__btn{
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		text-align: center;
		margin-top: -20px;
		opacity: 0;
		transition: all 300ms ease 0s;
	}
	&__buy{
		color: #FFF;
		background-color: #ff1c3d;
		padding: 19px 22px;
		font-size: 12px;
		font-weight: 400;
		text-transform: uppercase;
		display: inline-block;
		&:hover{
			opacity: 0.7;
		}
	}
	&__hidden{
		position: absolute;
		bottom: 10px;
		left: 0;
		right: 0;
		margin: auto;
		width: 100px;
		text-align: center;
		opacity: 0;
		transition: all 300ms ease 0s;
		cursor: pointer;
	}
	&__title{
		color: #140502;
		margin-top: 15px;
		font-size: 18px;
		font-weight: 400;
		line-height: 20px;
		display: block;
		&:hover{
			color: #FF1C3D;
		}
	}
	&__coast{
		margin-top: 5px;
		.item{
			&__text{
				color: #140502;
				font-size: 12px;
				font-weight: 300;
				display: inline-block;
				min-width: 67px;
			}
			&__rent{
				display: inline-block;
				color: #FF1C3D;
				font-size: 24px;
				font-weight: 700;
				span{
					color: #140502;
					font-size: 12px;
					font-weight: 400;
				}
			}
			&__price{
				display: inline-block;
				color: #140502;
				font-size: 18px;
				font-weight: 700;
				span{
					color: #140502;
					font-size: 12px;
					font-weight: 400;
				}
			}
		}
	}
	.slick-list{
		margin: 0 -10px;
	}
	.slick-prev{
		position: absolute;
		top: -45px;
		right: 60px;
		background: url(../img/icon/arrow-right.png) 0 0 no-repeat;
		font-size: 0;
		border: none;
		width: 8px;
		height: 13px;
		transform: rotate(180deg);
		transition: all 300ms ease 0s;
		outline: none;
		&:hover{
			background: url(../img/icon/arrow-right-red.png) 0 0 no-repeat;
		}
		&::-moz-focus-inner {
			border: 0;
		}
	}
	.slick-next{
		position: absolute;
		top: -45px;
		right: 0;
		background: url(../img/icon/arrow-right.png) 0 0 no-repeat;
		font-size: 0;
		border: none;
		width: 8px;
		height: 13px;
		transition: all 300ms ease 0s;
		outline: none;
		&:hover{
			background: url(../img/icon/arrow-right-red.png) 0 0 no-repeat;
		}
		&::-moz-focus-inner {
			border: 0;
		}
	}
	.catalog__view{
		&:before{
			bottom: 4px;
		}
	}
}
.reviews{
	margin-top: 68px;
	.main_title{
		float: left;
		background:#FFF;
		position: relative;
		padding-right: 20px;
	}
	&__all{
		float: right;
		display: block;
		margin-top: 18px;
		background:#FFF;
		position: relative;
		padding-left: 15px;
		.icon{
