ul li{
	list-style: none;
}
body {
  	background:#FFFFFF;
    font-family:'Affect';
}
.container {
	min-width: 320px;
	overflow: hidden;
}
.wrapper {
	max-width: 1220px;
	margin: auto;
	padding: 0 20px;
}
.main_title{
	color: #140502;
	font-size: 38px;
	font-weight: 300;
	text-transform: uppercase;
	position: relative;
}
@keyframes move {
	0%{
		opacity: 0;
	}
	50%{
		opacity: 0.5;
	}
	100%{
		opacity: 1;
	}
}
@keyframes run {
	0%{
		opacity: 1;
	}
	50%{
		opacity: 0.5;
	}
	100%{
		opacity: 0;
	}
}
.slick-slide{
	outline: none;
}
.catalog-main__item-info .inside__top .right__input::-webkit-input-placeholder {color:#140502; opacity: 1;}
.catalog-main__item-info .inside__top .right__input::-moz-placeholder          {color:#140502; opacity: 1;}
.catalog-main__item-info .inside__top .right__input:-moz-placeholder           {color:#140502; opacity: 1;}
.catalog-main__item-info .inside__top .right__input:-ms-input-placeholder      {color:#140502; opacity: 1;}
.fancybox-overlay{
	background: rgba(0,0,0,.5);
}
.fancybox-close, .fancybox-prev span, .fancybox-next span{
	background: url(../img/fancybox_sprite.png) 0 0 no-repeat;
}
#fancybox-loading div {
	width: 24px;
	height: 24px;
	background: url('../img/fancybox_loading.gif') center center no-repeat;
}
#vk_groups{
	width: 100% !important;
	iframe{
		width: 100% !important;
	}
}
p{
	color: #140502;
	font-size: 12px;
	line-height: 13px;
	font-weight: 300;
	margin-bottom: 15px;
	.alignleft{
		float: left;
		margin: 0 40px 20px 0;
	}
}
h1{
	color: #140502;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	margin-bottom: 37px;
}
h2{
	color: #140502;
	margin-bottom: 23px;
	font-size: 24px;
	font-weight: 700;
}
h3{
	color: #140502;
	margin-bottom: 20px;
	font-size: 18px;
	font-weight: 700;
	margin-top: 40px;
}
ul{
	width: 35%;
	display: inline-block;
	vertical-align: top;
	li{
		color: #140502;
		font-size: 12px;
		line-height: 13px;
		font-weight: 300;
		margin-bottom: 15px;
		position: relative;
		padding-left: 20px;
		&:before{
			position: absolute;
			left: 0;
			top: 5px;
			content: '';
			background-color: #ff364e;
			border-radius: 50%;
			width: 4px;
			height: 4px;
		}
	}
}
ol{
	counter-reset: myCounter;
	width: 35%;
	display: inline-block;
	vertical-align: top;
	li{
		list-style: none;
		color: #303030;
		font-size: 12px;
		font-weight: 300;
		margin-bottom: 23px;
		line-height: 16px;
		position: relative;
		padding-left: 40px;
		&:before{
			counter-increment: myCounter;
  			content:counter(myCounter);
  			color: #FFF;
			font-size: 12px;
			font-weight: 400;
			background-color: #ff1c3d;
			min-width: 20px;
			min-height: 20px;
			text-align: center;
			line-height: 20px;
			position: absolute;
			left: 0;
			top: -5px;
		}
	}
}
h4{
	color: #140502;
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 20px;
}
tr td{
	font-weight: 700;
	color: #140502;
	font-size: 12px;
	height: 40px;
	text-align: center;
	&:first-child{
		text-align: left;
		padding-left: 20px;
	}
}
tr:first-child{
	
	font-size: 14px;
	height: 40px;
	text-align: left;
	background-color: #ff364e;
	text-align: center;
	&:first-child{
		text-align: left;
		padding-left: 20px;
		width: 250px;
	}
}
tr:first-child td{
	font-weight: 700;
	color: #FFF;
}
table{
	width: 70%;
	float: left;
	margin-bottom: 60px;
	tr{
		&:nth-child(2n+3){
			background-color: #f9ece3;
		}
	}
}
table,tbody, tfoot, thead, tr, th, td{
	border: 1px solid #f9ece3;
}
.alignright{
	float: right;
}
blockquote{
	background-color: #f8f5f3;
	clear: both;
	position: relative;
	min-height: 122px;
	line-height: 122px;
	p{
		color: #938984;
		font-size: 12px;
		font-weight: 300;
		display: inline-block;
		width: 100%;
		vertical-align: middle;
		padding: 10px 10px 10px 140px;
		margin-bottom: 0;
		line-height: 14px;
	}
	&:before{
		position: absolute;
		top: 40px;
		left: 40px;
		background: url(../img/icon/quote.png) 0 0 no-repeat;
		width: 37px;
		height: 37px;
		content: '';
		z-index: 1;
	}
	&:after{
		width: 120px;
		height: 120px;
		background-color: #f9ece3;
		position: absolute;
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
		content: '';
	}
}